import { Component, Injectable, OnInit, ViewChild } from '@angular/core';
import { NavController, Events } from '@ionic/angular';
import { AppMin,  } from '../core';

import { Router } from '@angular/router';





@Injectable()
@Component({
  selector: 'home-pinglun',
  templateUrl: 'home-pinglun.page.html',
  styleUrls: ['home-pinglun.page.scss']
})
export class HomePinglunPage implements OnInit {
  @ViewChild("myBox") mybox: any;

  header_class;
  loader;

  addSum = 0;//加载次数 
  pinglList = [];//数据列表
  isadd = false;
  refresher;
  infiniteScroll;




  constructor(public navCtrl: NavController, public nav: NavController, public router: Router, public events: Events, public appMin: AppMin, public appmin: AppMin) {


    if (this.appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class_md";
   //   console.log("判断为安卓系统 设置状态栏样式")
    }

  }

  ionViewWillEnter() {


  }
  toObjpage(objid) {
  //  console.log(objid);
    let obj = {
      userId: objid
    }
    // this.navCtrl.push(Consultant, obj);
    this.router.navigate(['consultant'], { queryParams: obj });
    //this.nav.navigateForward("consultant")
    // this.navCtrl.
  }
  addpinglList(iss?: Boolean) {
    //加载评论列表
    this.isadd = true;
    let obj = {
      type: 'addpinglList',
      addSum: this.addSum,

    }

    // this.appMin.send(JSON.stringify(obj));
    // console.log("发送addpinglList" + JSON.stringify(obj));

    this.appMin.get(obj).subscribe((data) => {
    //  console.log("addpinglList=" + JSON.stringify(data));
      if (iss) {
        this.pinglList = [];
      }
      this.dataHandle(data);
    })


  }
  doRefresh(refresher) {
    //顶部刷新
  //  console.log('Begin async operation', refresher);
    this.refresher = refresher;


    this.addSum = 0;

    this.addpinglList(true);

    setTimeout(() => {
    //  console.log('Async operation has ended');

    }, 2000);

  }
  doInfinite(infiniteScroll) {
    //底部刷新加载
    this.infiniteScroll = infiniteScroll;
    if (this.isadd) {
      infiniteScroll.target.complete();
      return;
    }
    this.addpinglList();



    // if (infiniteScroll) {
    //   infiniteScroll.target.complete();
    // }
  }
  dataHandle(msg, isSave?: boolean) {

    if (this.refresher) {
      this.refresher.target.complete();
    }
    if (this.infiniteScroll) {
      this.infiniteScroll.target.complete();
    }

    if (msg.type == 'addpinglList_ret') {
      //   console.log("收到addpinglList_ret" + JSON.stringify(msg))

      for (let i = 0; i < msg['rows'].length; i++) {
        this.pinglList.push(msg['rows'][i]);
      }

     // console.log(this.pinglList)

      this.isadd = false;
      this.addSum = parseInt(msg['pages']) + 1;
      //  console.log("addSum=" + this.addSum);
    }

  }
  ngOnInit() {
    this.addpinglList();
    // console.log("dom0=" + this.mybox);
  }
  ngAfterViewInit() {
    //dom 加载完成
    var this1 = this;
    // console.log("dom0=" + this1.mybox);

  }
}
